let sign = require("../../commons/sign.js");
let _this, page = 1,
	catid = 0,
	isEnd = false;
export default {
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,

			categories: [{
				cateId: 0,
				name: "全部"
			}],
			artList: [],
			host: ''
		}
	},
	onLoad() {
		_this = this;
		_this.host = _this.staticServer;
		// 加载文章分类
		uni.request({
			url: _this.apiServer + 'category&m=index',
			method: 'GET',
			success: res => {
				if (res.data.status == 'ok') {
					var categories = res.data.data;
					for (var k in categories) {
						_this.categories.push({
							cateId: k,
							name: categories[k]
						});
					}
					_this.getArticleList();
				} else {
					uni.showToast({
						title: res.data.data,
						icon: "none"
					})
				}
			}
		});
	},
	onPullDownRefresh() {
		_this.init();
		_this.getArticleList();
	},
	onReachBottom() {
		_this.getArticleList();
	},
	methods: {
		init() {
			page = 1;
			isEnd = false;
			_this.artList = [];
		},
		tabSelect(e) {
			_this.TabCur = e.currentTarget.dataset.id;
			_this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			catid = _this.categories[_this.TabCur].cateId;
			_this.init();
			_this.getArticleList();
		},
		getArticleList() {
			if (isEnd) {
				return false;
			}
			uni.showLoading({
				title: "正在加载...",
				mask: true
			})
			uni.request({
				url: _this.apiServer + 'art&m=getList',
				method: "GET",
				data: {
					cate: catid,
					page: page
				},
				success(res) {
					if (res.data.status == 'empty') {
						isEnd = true;
						uni.showToast({
							title: "已全部加载文章",
							icon: "none"
						})
					} else if (res.data.status == 'ok') {
						_this.artList = _this.artList.concat(res.data.data);
						page++;
					}
				},
				complete() {
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			})
		}
	}
}
